.Customization {
  text-align: center;
}

.customizationOptions {
  position: fixed; /* or absolute */
  top: 11vh;
  left: 15vw; /* or adjust positioning as needed */
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  max-width: 70vw; /* Adjust max-width as needed */
}
.colorSelection {
  display: flex;
  gap: 12px;
  flex-wrap: wrap; 
  justify-content: flex-start; 
  width: 100%;
}

@media (min-width: 500px) {
  .customizationOptions {
    left: 720px; /* Reset left positioning */
    right: 1px; /* Adjust right positioning as needed */
  }
}

.customizeTitle {
  font-size: 4vh;
  margin-bottom: 3vh;
}

.colorLabel {
  font-size: 3vh;
}

.color {
  width: 100px;
  height: 100px;
  border: 1px solid black;
  margin-right: 10px; /* Add a right margin to create space between color boxes */
  margin-bottom: 10px; /* Add a bottom margin to create space between rows */
  cursor: pointer;
}

.color:hover {
  cursor: pointer;
  border: 1px solid lightgreen;
}

.blue {
  background-color: #3f48cc;
}
.green {
  background-color: #0ed145;
}
.red {
  background-color: #ec1c24;
}
.white {
  background-color: #ffffff;
}
.yellow {
  background-color: #fff200;
}
.orange {
  background-color: #ff7f27;
}
.july {
  background: linear-gradient(red, blue, white);
}
.true {
  border: 3px solid black;
}

.startContainer {
  position: absolute;
  bottom: 7vh;
  width: 100%;
}

.startButton {
  font-size: 6vh;
  padding: 2vh;
  background-color: #a3c78d;
}

@media screen and (orientation: portrait) {
  .customizationOptions {
    position: fixed; 
    top: 34vh;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    bottom: 5vh; /* Position at the bottom for vertical orientation */
    left: 50%; /* Center horizontally */
    transform: translateX(-50%); /* Move left by 50% of its own width */
    gap: 2vh; /* Adjust gap between elements */
    max-width: 120vw;
  }

  .color {
    width: 50px;
    height: 50px;
    border: 1px solid black;
    margin-right: 1px; /* Add a right margin to create space between color boxes */
    margin-bottom: 1px; /* Add a bottom margin to create space between rows */
    cursor: pointer;
  }

  .customizeTitle {
    font-size: 4vh;
    margin-bottom: 0;
  }

}